import React from "react";
import styled from "@emotion/styled/macro";
import { Route, Switch } from "react-router-dom";
import { Helmet } from "react-helmet";

import Sidebar from "./Sidebar";
import Footer from "./Footer";
import Playing from "./Playing";
import Controls from "./Controls";
import AdditionalControls from "./AdditionalControls";
import Home from "./Home";
import Collection from "./Collection";
import { DispatchContext, useAppState } from "./state";
import { ICollection } from "./models";

const Main = styled.main`
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
`;

const Section = styled.section`
  flex: 1 1;
  overflow: auto;
  scrollbar-width: thin;
  display: flex;
  flex-direction: column;
  background-color: #000;
  padding-bottom: 90px;
  @media screen and (min-width: 768px) {
    max-height: calc(100vh - 90px);
    background-color: #121212;
  }
`;

const sortByReleaseYear = (a: ICollection, b: ICollection) => {
  return a.releaseYear > b.releaseYear
    ? -1
    : a.releaseYear < b.releaseYear
    ? 1
    : 0;
};

function Spotify() {
  const [{ collections, tracks, currentTrack, currentCollection }, dispatch] =
    useAppState();

  const albums = collections
    .filter((col) => col.type === "Album")
    .sort(sortByReleaseYear);

  const singles = collections
    .filter((col) => col.type === "Single")
    .sort(sortByReleaseYear);

  const playlists = collections
    .filter((col) => col.type === "Playlist")
    .sort(sortByReleaseYear);

  const popularTracks = tracks
    .sort((a, b) => {
      return a.views > b.views ? -1 : a.views < b.views ? 1 : 0;
    })
    .slice(0, 10);

  const track = tracks.find((t) => t.id === currentTrack);
  if (track) {
    track.collection = collections.find((col) => col.id === track.collectionId);
  }
  
  return (
    <DispatchContext.Provider value={dispatch}>
      <Helmet>
        <title>
          {track ? `${track.title} • Arthur A. Ferreira` : "Artify"}
        </title>
      </Helmet>
      <Main>
        <Sidebar collections={collections} />
        <Section>
          <Switch>
            <Route exact path="/artify/">
              <Home
                albums={albums}
                singles={singles}
                playlists={playlists}
                popularTracks={popularTracks}
                currentTrack={currentTrack}
                currentCollection={currentCollection}
              ></Home>
            </Route>
            <Route path="/artify/:id">
              <Collection
                collections={collections}
                currentTrack={currentTrack}
                tracks={tracks}
              ></Collection>
            </Route>
          </Switch>
        </Section>
        <Footer
          hasTrack={!!track}
          baseColor={track?.collection?.baseColor || "#595341"}
        >
          <Playing isPlaying={false} track={track} />
          <Controls />
          <AdditionalControls />
        </Footer>
      </Main>
    </DispatchContext.Provider>
  );
}

export default Spotify;
