function reducer(state: ISpotifyState, action: IAction) {
  switch (action.type) {
    case "NEXT_SONG":
      return { ...state };
    case "PREV_SONG":
      return { ...state };
    case "PLAY_QUEUE":
      const [currentTrack] = action.queue;
      console.log(action.queue);
      return {
        ...state,
        currentTrack: currentTrack,
        queue: action.queue,
        currentCollection: action.currentCollection,
      };
    default:
      return state;
  }
}

export function useAppState(): [ISpotifyState, React.Dispatch<IAction>] {
  const [state, dispatch] = useReducer(reducer, initialState);
  return [state, dispatch];
}

export const DispatchContext = React.createContext<
  ISpotifyState | React.Dispatch<IAction>
>(initialState);
