import React, { useState } from "react";
import styled from "@emotion/styled/macro";
import { css } from "@emotion/css";

import { ReactComponent as RandomIcon } from "../svgs/regular/random.svg";
import { ReactComponent as BackwardIcon } from "../svgs/solid/backward.svg";
import { ReactComponent as PausedIcon } from "../svgs/solid/pause.svg";
import { ReactComponent as PlayIcon } from "../svgs/solid/play.svg";
import { ReactComponent as ForwardIcon } from "../svgs/solid/forward.svg";
import { ReactComponent as RepeatIcon } from "../svgs/regular/repeat-alt.svg";

const IconButton = styled.button`
  display: none;
  @media screen and (min-width: 768px) {
    height: 32px;
    width: 32px;
    fill: #b2b2b2;
    background-color: transparent;
    border: none;
    margin: 0 4px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      fill: #fff;
    }
    &:disabled {
      fill: #535353;
    }
  }
`;

const RoundedIconButton = styled.button`
  display: none;
  @media screen and (min-width: 768px) {
    fill: #000;
    background-color: #fff;
    border: none;
    margin: 0 4px;
    padding: 0;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    &:active {
      transform: scale(0.95);
    }
  }
`;

const svgSmall = css`
  height: 16px;
  width: 16px;
`;

const svgPlayIcon = css`
  margin-left: 2px;
`;

const Container = styled.div`
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
`;

const ProgressContainer = styled.div`
  flex: 0 0 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (min-width: 768px) {
    margin-top: 16px;
  }
`;

const Progress = styled.progress`
  flex: 1 1 auto;
  margin: 0 2px;
  appearance: none;
  height: 2px;
  border: none;
  border-radius: 2px;
  background-color: #535353;
  ::-webkit-progress-bar {
    background-color: #535353;
    border-radius: 2px;
  }

  ::-webkit-progress-value {
    background-color: #b3b3b3;
    border-radius: 2px;
  }

  ::-moz-progress-bar {
    background-color: #b3b3b3;
    border-radius: 2px;
  }

  @media screen and (min-width: 768px) {
    height: 4px;
    max-width: 65%;
    margin: 0 8px;
    :hover::-webkit-progress-value {
      background-color: #1db954;
    }
    :hover::-moz-progress-bar {
      background-color: #1db954;
    }
  }
`;

const Span = styled.span`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline;
    font-size: 11px;
    line-height: 16px;
    font-weight: 400;
    color: rgb(179, 179, 179);
  }
`;

function Controls() {
  const [isPlaying, setIsPlaying] = useState(false);

  return (
    <Container>
      <IconButton type="button" disabled>
        <RandomIcon className={svgSmall} />
      </IconButton>
      <IconButton type="button">
        <BackwardIcon className={svgSmall} />
      </IconButton>
      <RoundedIconButton
        onClick={() => {
          setIsPlaying(!isPlaying);
        }}
      >
        {isPlaying ? (
          <PausedIcon className={svgSmall} />
        ) : (
          <PlayIcon className={`${svgSmall} ${svgPlayIcon}`} />
        )}
      </RoundedIconButton>
      <IconButton type="button">
        <ForwardIcon className={svgSmall} />
      </IconButton>
      <IconButton disabled type="button">
        <RepeatIcon className={svgSmall} />
      </IconButton>
      <ProgressContainer>
        <Span>00:00</Span>
        <Progress max="100" value="50"></Progress>
        <Span>00:05</Span>
      </ProgressContainer>
    </Container>
  );
}

export default Controls;
